import clsx from 'clsx';
import {fade, makeStyles, Paper, Typography, useTheme, Grid, Divider} from "@material-ui/core";
import {inject, observer} from "mobx-react";
import React from 'react';
import PastMember from "./PastMember";
import Student from './Student';
import Section from "../../CompLib/Section";

const useStyles = makeStyles(theme=>({
    root:{
        borderColor:theme.palette.secondary.main,
        borderStyle: 'solid',
        borderWidth: 'thin',
    },
}))

function PastMembers({system,data,students,title}){
    const classes=useStyles({isMobile: system.isMobile});
    const theme=useTheme();
    return <Section title={title}>
            <Paper elevation={0} className={classes.root}>
                {students.map((student,pid)=>{
                    return <PastMember student={student} key={pid} isLastone={pid===students.length-1}/>
                })}
            </Paper>
        </Section>
}

export default inject('system', 'data')(observer(PastMembers));