import clsx from 'clsx';
import {fade, makeStyles, Paper, Typography, useTheme, Grid, Divider} from "@material-ui/core";
import {inject, observer} from "mobx-react";
import React from 'react';

const useStyles = makeStyles(theme=>({
    root:{
        display:'flex',

    },
    name: {
        flex:'1 100px',
        fontWeight: 'bolder',
        fontSize: props => props.isMobile ? 14 : 15,
        color: theme.palette.getContrastText(theme.palette.background.default),
        textAlign: 'center',
    },
    hasLink: {
        cursor: 'pointer'
    },
    text:{
        paddingTop:'1%',
        paddingBottom:'0.5%',
        margin:'auto',
    },
    year: {
        flex:'0.5 50px',
        // position: 'absolute',
        // top: 6,
        color: theme.palette.secondary.main,
        // right: 8,
        textAlign: 'center',
    },
    degree:{
        flex:'1 100px',
        textAlign: 'center',
        color: theme.palette.secondary.main,
    },
    job:{
        flex:'2 200px',
        textAlign: 'center'
    },
    divider:{
        marginLeft: '1%',
        marginRight: '1%',
    },
    verticalDiverder:{
        marginTop: '1%',
        marginBottom: '1%',
    }
}))

function PastMember({system,data,student,isLastone}){
    const classes=useStyles({isMobile: system.isMobile});
    const theme=useTheme();
    const {url,name,title,degree,graduateYear}=student;
    return <React.Fragment>
            <div
            onClick={() => !!url && window.open(url)}
            className={clsx(classes.root, {[classes.hasLink]: !!url})}>
                        <Typography className={clsx(classes.name,classes.text)}>{name}</Typography>
                        <Divider className={classes.verticalDiverder} orientation="vertical" flexItem />
                        <Typography className={clsx(classes.degree,classes.text)}>{degree}</Typography>
                        <Divider className={classes.verticalDiverder} orientation="vertical" flexItem />
                        <Typography className={clsx(classes.year,classes.text)}>{graduateYear}</Typography>
                        <Divider className={classes.verticalDiverder} orientation="vertical" flexItem />
                        <Typography className={clsx(classes.job,classes.text)}>{student.job||""}</Typography>
            </div>
            {(!isLastone) && <Divider className={classes.divider}/>}
        </React.Fragment>
        
}

export default inject('system', 'data')(observer(PastMember));